form {
  align-items: stretch;
  display: flex;
  flex-flow: column nowrap;
  margin: 50px auto;
}

form label {
  color: #02a9ea;
  font-family: "Fenomen Sans Bold";
  font-size: .8rem;
  margin-bottom: 0;
  padding-bottom: 0;
  text-transform: uppercase;
}

form input,
form select {
  border: 0;
  font-family: "Fabrikat Mono";
}

form input::placeholder,
form select::placeholder {
  font-family: "Fabrikat Mono";
}

form input:focus,
form select:focus {
  background-size: 40px;
  text-shadow: 0.07rem 0.07rem #fff, -0.07rem 0.07rem #fff, -0.07rem -0.07rem #fff, 0.07rem -0.07rem #fff;
  border: 0;
  outline: none;
}

form input:-webkit-autofill,
form input:-webkit-autofil:focus,
form select:-webkit-autofill,
form select:-webkit-autofil:focus {
  background-color: #fff;
}

form select {
  background: #fff;
  border-bottom: 2px solid #777777;
}

form select:focus {
  border-bottom: 2px solid #02a9ea;
}

form input[type=text],
form input[type=email],
form input[type=password] {
  border-bottom: 2px solid #777777;
}

form textarea.form-control {
  border: 2px solid #777777;
}

form input[type=text],
form input[type=email],
form input[type=password],
form textarea.form-control {
  width: 100%;
}

form input[type=text]:focus,
form input[type=email]:focus,
form input[type=password]:focus,
form textarea.form-control:focus {
  border-color: #02a9ea;
}

form .btn {
  align-self: flex-end;
}

form .col,
form .col-4,
form .col-sm-5,
form .col-8,
form .col-xs-12 {
  padding: 0;
}

.form-control {
  border: 0;
  border-radius: 0;
}

.form-control-file {
  margin-top: 10px;
}

.form-group {
  margin: 10px 0 30px;
}

small.form-text {
  padding-top: 6px;
}

.embedded-form,
.upload-form {
  max-width: 350px;
}

.track-submission-form {
  max-width: 500px;
}

.stretch-select-stack {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  padding-left: 5px !important;
}

.stretch-select-stack select {
  padding: .5rem .75rem;
}




